.data-output-modal {
  background-color: #fff;
  height: 37rem;
  display: inline-block;
  overflow-x: auto;
  white-space: nowrap;
  max-width: 100%;
  border: 0;
}

.dataom-title-container {
  align-items: center;
  background-color: #eff7fe;
  border-bottom: solid 4px #f2f2f2;
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}

.dataom-modal-title {
  color: #3b8ed6;
}

.dataom-x-button {
  background-color: #f00;
  color: #fff;
  margin: 8px;
  outline: none;
}

.dataom-table-container {
  height: 85%;
  align-items: flex-start;
  display: flex;
  flex-direction: column;
}

.dataom-under-construction-container {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 80%;
  justify-content: space-around;
  width: 100%;
}

.dataom-under-construction-icon {
  border: solid 4px #3b8ed6;
  border-radius: 30px;
  height: 100px;
  margin-top: 50px;
  padding: 20px;
  width: 100px;
}

.dataom-under-construction-msg {
  color: #3b8ed6;
  margin-top: 0;
}