.Simulator {
  height: 100%;
}

.CRRT-title {
  background-color: #c8e9ff;
  color: #3b8ed6;
  height: 38px;
  margin: 0;
  /* font-family: 'Open Sans', sans-serif; */
  /* font-family: "Roboto", sans-serif; */
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  padding: 21px;
}

.CRRT-subtitle-container {
  align-items: center;
  display: flex;
  justify-content: space-around;
  


  width: 22rem;
  border: none;
  border-radius: 10px;
  height: 2rem;
  margin: 0 auto;
  background-color: #fff;
}

.CRRT-subtitle {
  color: #4c4646;
  font-family: "Montserrat", sans-serif;
  margin-bottom: 4px;
  margin-left: 0;
  margin-right: 0;
  margin-top: 4px;
  font-size: 1rem;
  font-weight: 400;
}

.CRRT-subtitle-span {
  font-weight: 700;
}

/* .dataOutputContainer {
  display: flex;
  height: 100vh;
  width: 100vw;
} */

.dataOutputContainer {
  display: grid;
  grid-template-columns: 16rem 1fr;
  height: 100%;
}

.btn-active,
.data-output-btn,
.physical-exam-output-btn {
  height: 100%;
  outline: none;
  /* width: 160px; */
}

.new-orders-btn-active {
  background-color: #c8e9ff;
  border: solid 1px #3b8ed6;
  color: #3b8ed6;
}

.btn-active {
  border-bottom: solid 2px #000;
  border-left: solid 2px #000;
  border-right: solid 2px #000;
  border-top: solid 2px #000;
  font-size: 1rem;
  font-weight: bold;
}

.data-output-btn {
  border-left: solid 1px #000;
  border-right: solid 1px #000;
  color: #000;
  font-size: 1rem;
}

.physical-exam-output-btn {
  background-color: #fff;
  border-bottom: solid 1px #000;
  border-left: solid 1px #000;
  border-right: solid 1px #000;
  border-top: solid 1px #000;
  color: #000;
  font-size: 1rem;
}

.modalBtnContainer {
  background-color: #eff7fe;
  display: flex;
  flex-direction: column;
  height: 37rem;
  justify-content: center;
  /* width: 250px; */
}

.modal-container {
  /* height: 80.5%; */
  /*Maybe try?*/
  overflow-x: scroll !important;
  overflow-y: scroll !important;
  /* width: 82.35%; */
}

.simulator-header {
  background-color: #c8e9ff;
  border-bottom: solid 1px #3b8ed6;
}

.form-buttons-container {
  background-color: #c8e9ff;
  width: 85%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.orders-btn {
  background-color: #f00;
  border-radius: 5rem;
  color: #fff;
  cursor: pointer;
  font-size: 1rem;
  height: 2rem;
  margin: 1rem 1rem 1rem 0;
  outline: none;
  width: 5rem;
}

.orders-btn:hover {
  box-shadow: 0px 0px 10px #d0d6d3;
  background-color: #3b8ed6;
}

.orders-btn:active {
  background-color: #fff;
  border: solid 2px #3b8ed6;
  color: #3b8ed6;
}

.orders-btn:disabled {
  background-color: #e5e5e5;
  border: solid 2px #e5e5e5;
  color: #fff;
  cursor: not-allowed;
}

.crrt-display-btn,
.restart-case-btn {
  color: #4c4646;
}

.crrt-display-btn:hover,
.restart-case-btn:hover {
  color: #fff;
  background-color: #4c4646;
}

.crrt-display-btn:active,
.restart-case-btn:active {
  background-color: #fff;
  border: solid 2px #4c4646;
  color: #4c4646;
}

.restart-case-btn:disabled {
  background-color: #e5e5e5;
  border: solid 2px #e5e5e5;
  color: #fff;
  cursor: not-allowed;
}

